import React from 'react';
import PropTypes from 'prop-types';

import { FieldRow } from '@mssgme/ui';
import { AnchorSelect } from './AnchorSelect';

export function AnchorField({ onChange, website, ...props }) {
    return (
        <FieldRow {...props}>
            {({ field: { name, value, ...field }, form: { errors, touched, setFieldValue } }) => (
                <AnchorSelect
                    {...field}
                    name={name}
                    value={value}
                    hasError={errors[name] && touched[name]}
                    onChange={onChange || setFieldValue}
                    website={website}
                />
            )}
        </FieldRow>
    );
}

AnchorField.propTypes = {
    onChange: PropTypes.func,
    website: PropTypes.object.isRequired,
};
