import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { useToggle } from 'react-use';
import { AnimatePresence } from 'framer-motion';

import { ChevronDownIcon } from '@mssgme/icons';
import AnchorSelector from './AnchorSelector';
import PageSelector from './PageSelector';

import styles from './AnchorSelect.scss';

export const AnchorSelect = ({
    website,
    onChange,
    name,
    value,
}) => {
    const { t } = useTranslation('admin-ui');
    const page = website.pages.find(({ id }) => id === value.page);
    const block = page?.blocks.find(({ _id }) => _id === value.block);
    const blockIndex = page?.blocks.findIndex(({ _id }) => _id === value.block) + 1;
    const [isAnchorModal, toggleAnchorModal] = useToggle(false);
    const [isPageModal, togglePageModal] = useToggle(false);

    return (
        <>
            <div className={styles.input} onClick={toggleAnchorModal}>
                <div>{page && block && `#${blockIndex} ${t(`landings:labels.block.kinds.${block.kind}`)} (${page.name})`}</div>
                <div><ChevronDownIcon size={14}/></div>
            </div>
            <AnimatePresence>
                {isAnchorModal && (
                    <AnchorSelector
                        key="block"
                        name={name}
                        value={value}
                        onClose={toggleAnchorModal}
                        togglePageModal={togglePageModal}
                        website={website}
                        page={page}
                        onChange={onChange}
                    />
                )}
                {isPageModal && (
                    <PageSelector
                        key="page"
                        name={name}
                        value={value}
                        onClose={togglePageModal}
                        website={website}
                        onChange={onChange}
                    />
                )}
            </AnimatePresence>
        </>
    );
};

AnchorSelect.propTypes = {
    website: PropTypes.object.isRequired,
    onChange: PropTypes.func.isRequired,
    value: PropTypes.object.isRequired,
    name: PropTypes.string.isRequired,
};
