import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import { __t } from '@mssgme/shared';
import { ListInput, ModalSheet, ModalSheetHeader } from '@mssgme/ui';

import styles from './PageSelector.scss';

const PageSelector = ({ name, value, onChange, onClose, website, ...rest }) => {
    const { t } = useTranslation('ui');
    const { pages, homePageId, finalUrl } = website;
    const labelItem = (name, finalUrl) => <div className={styles.labelItem}>
        <div className={styles.labelName}>{name}</div>
        <div className={styles.labelUrl}>{finalUrl}</div>
    </div>;
    const options = pages
        .filter(({ enabled, blocks }) => enabled && blocks.length > 0)
        .map((page) => {
            const isHomePage = page._id === homePageId;
            const data = isHomePage ? {
                name: __t('homePage'),
                finalUrl: finalUrl,
            } : {
                name: page.name,
                finalUrl: finalUrl + `/${page.slug}`,
            };

            return {
                value: page._id,
                label: labelItem(data.name || t('labels.unnamedPage'), data.finalUrl),
                subLabel: data.finalUrl,
            };
        });

    const handleSelect = (pageId) => {
        onChange(name, { page: pageId, block: pages.find(({ _id }) => _id === pageId).blocks[0]._id });
        onClose();
    };

    return (
        <ModalSheet {...rest} onClose={onClose}>
            <div className={styles.root}>
                <ModalSheetHeader
                    title={t('PageSelector.title')}
                    closePath={onClose}
                />
                <div className={styles.selector}>
                    <ListInput options={options} onChange={(data) => handleSelect(data)} value={value.page}/>
                </div>
            </div>
        </ModalSheet>
    );
};

PageSelector.propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.object.isRequired,
    onChange: PropTypes.func.isRequired,
    onClose: PropTypes.func.isRequired,
    website: PropTypes.object.isRequired,
};

export default PageSelector;
