import { useCallback, useRef } from 'react';
import { useMeasure } from 'react-use';

export const useScrollTo = (delay = 300) => {
    const contentRef = useRef(null);

    const scrollTo = useCallback((to, behavior = 'smooth', hash) => {
        const container = contentRef.current;

        if (!container) {
            return;
        }

        let top = 0;

        switch (to) {
            case 'top': top = -container.scrollTop; break;
            case 'bottom': top = container.scrollHeight - container.clientHeight; break;
        }


        const timer = setTimeout(() => {
            const container = contentRef.current;
            const hashEl = hash && container?.querySelector(hash);

            if (hashEl) {
                return hashEl.scrollIntoView({ behavior, block: 'start' });
            }

            if (to) {
                return container.scroll({ top, behavior });
            }
        }, delay);

        return () => {
            clearTimeout(timer);
        };
    }, []);

    return [contentRef, scrollTo];
};
